import Vue from 'vue'
import Router from 'vue-router'
import workRequest from '@/components/Hr_portal/workRequest/workRequest'

Vue.use(Router)

export default new Router({
  // mode:'history',
  routes: [
    {
      path: '/work-request',
      name: 'workRequest',
      component: workRequest
    }
  ]
});



