<style src='./assets/layout/css/bootstrap-material-datetimepicker.css'></style>
<style src='./assets/layout/css/bootstrap.min.css'></style>
<style src='./assets/layout/css/jQuery-UI.css'></style>
<style src='./assets/layout/css/jquery-ui.multidatespicker.css'></style>
<style src='./assets/layout/css/material-icons.css'></style>
<style src='./assets/layout/css/MaterialDesignIcons.css'></style>
<style src='./assets/layout/css/nice-select.css'></style>
<style src='./assets/layout/css/slick-theme.css'></style>
<style src='./assets/layout/css/slick.css'></style>
<style src='./assets/layout/css/main.css'></style>


<template>
  <div id="app">
    <div class="page-container expand">
        <side-bar></side-bar>
        <div class="main-contnet">
          <upper-nav></upper-nav>
          <router-view/>
        </div> 
    </div>
  </div>
</template>

<script>

import "./assets/layout/js/jquery.slimscroll.min.js"
import "./assets/layout/js/bootstrap-material-datetimepicker.js"
import "./assets/layout/js/jquery-nice-select.js"
import './assets/layout/js/jquery-ui.multidatespicker.js'
import "./assets/layout/js/main.js"

$(function () {
  	// vars
	let topBarMenu = $('.topbar .menu .main-menu'),
		pageCont   = $('.page-container'),
		sidebar    = $('.sideBar');

	// sideBar
	$('.toggleBar').click(function () {
		$(sidebar).toggleClass('collapsed');
		if ($(window).width() > 767) {
			$(pageCont).toggleClass('expand');
		}
		$(sidebar).css("position","fixed")  
	});
	$('.close-btn').click(function () {
		$(sidebar).removeClass('collapsed'); 
	});

	$('body').on('click', '.sideBar ul li .sid-links', function (e) {
		$('.sideBar').find('.main-menu').removeClass('open');
		$(this).closest('.sideBar').find('.sub-menu').slideUp();
		$(this).closest('.sideBar').find('.sid-links').removeClass('for-a-active');
		$(this).addClass('for-a-active');
		$(this).find('> ul').slideToggle().parent().siblings().find('ul').slideUp();
	});

	// topBar
	$(topBarMenu).click(function (e) {
		e.stopPropagation();
		$(this).toggleClass('active');
		$(this).siblings().removeClass('active');
	});
	$('body').click(function () {
		$(topBarMenu).removeClass('active')
	});
});

import upperNav from '@/components/layout/upperNav'
import sideBar from '@/components/layout/sideBar'

export default {
  name: 'App',
  components: {upperNav,sideBar},
  data() {
    return {
      apiUrl:"http://localhost:50450/app/dist/"
    }
  }
}
</script>

<style>
.main-contnet {
  margin-top:60px;
}
.ui-datepicker-clear-month {
    position: absolute;
    top: 9px;
    right: 32px;
    height: 100%;
    line-height: 100%;
    display: inline;
    cursor: pointer;
    color: red!important;
}
.ui-state-active, .ui-widget-content .ui-state-active{
    background: #666 !important;
    color: #fff !important;
}
.ui-datepicker {
    width: 35em !important;
    padding: .2em .2em 0;
    display: none;
}
.ui-datepicker th {
    padding: .4em .2em !important;
}
.ui-datepicker td a {
    text-align: center !important;
}
.ui-widget {
    font-family: inherit !important;
    font-size: 1em !important;
}
</style>



